function webos() {
    window.dhxWindows = new dhtmlxWindowsPlugin();

    window.addEventListener('keydown', function (e) {
        if (e.keyCode === 27) {
            dhxWindows.closeOnTopWindow();
        }
    }, true);

    window.addEventListener('resize', function () {
        dhxWindows.resizeViewport();
    });
}

$(document).ready(function () {
    new webos();
    window.apps = { contextPath: "demo"}; 
    window.apps.settings = new settings();
    window.apps.dashboard = new dashboard();
    window.apps.board = new board();
    window.apps.help = new help();
    window.apps.explorer = new explorer();
});